  /*****************/ }

.step h1, .step .h1 {
  font-size: 124.8px; }

.step h2, .step .h2 {
  font-size: 103.2px; }

.step h3, .step .h3 {
  font-size: 81.6px; }

.step h4, .step .h4 {
  font-size: 62.4px; }

.step h5, .step .h5 {
  font-size: 48px; }

.step h6, .step .h6 {
  font-size: 40.8px; }

.impress-enabled .step {
  margin: 0;
  opacity: 0.15;
  -webkit-transition: opacity 1s;
  -moz-transition: opacity 1s;
  -ms-transition: opacity 1s;
  -o-transition: opacity 1s;
  transition: opacity 1s; }

.impress-enabled .step.active {
  opacity: 1;
  cursor: default; }

.impress-enabled .step a {
  color: rgba(255, 255, 255, 0.8);
  text-decoration: none;
  padding: 0 0.4em;
  background: #3e4852;
  border-radius: 0.2em;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s; }

.impress-enabled .step a:hover, .impress-enabled .step a:focus {
  color: #e5b560;
  background: #495561;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  text-decoration: none; }

.impress-enabled .step b {
  display: inline-block;
  -webkit-transition: all 0.5s;
  -moz-transition: all 0.5s;
  -ms-transition: all 0.5s;
  -o-transition: all 0.5s;
  transition: all 0.5s; }

.impress-enabled .step.present .positioning {
  -webkit-transform: translateY(-10px);
  -moz-transform: translateY(-10px);
  -ms-transform: translateY(-10px);
  transform: translateY(-10px);
  -webkit-transition-delay: 1.25s;
  -moz-transition-delay: 1.25s;
  -ms-transition-delay: 1.25s;
  -o-transition-delay: 1.25s;
  transition-delay: 1.25s; }

.impress-enabled .step.present .positioning-down {
  -webkit-transform: translateY(10px);
  -moz-transform: translateY(10px);
  -ms-transform: translateY(10px);
  transform: translateY(10px);
  -webkit-transition-delay: 1.25s;
  -moz-transition-delay: 1.25s;
  -ms-transition-delay: 1.25s;
  -o-transition-delay: 1.25s;
  transition-delay: 1.25s; }

.impress-enabled .step.present .rotating {
  -webkit-transform: rotate(-10deg);
  -moz-transform: rotate(-10deg);
  -ms-transform: rotate(-10deg);
  transform: rotate(-10deg);
  -webkit-transition-delay: 1.5s;
  -moz-transition-delay: 1.5s;
  -ms-transition-delay: 1.5s;
  -o-transition-delay: 1.5s;
  transition-delay: 1.5s; }

.impress-enabled .step.present .rotating-right {
  -webkit-transform: rotate(10deg);
  -moz-transform: rotate(10deg);
  -ms-transform: rotate(10deg);
  transform: rotate(10deg);
  -webkit-transition-delay: 1.5s;
  -moz-transition-delay: 1.5s;
  -ms-transition-delay: 1.5s;
  -o-transition-delay: 1.5s;
  transition-delay: 1.5s; }

.impress-enabled .step.present .scaling-up {
  animation-duration: 10s;
  animation-name: changescale;
  animation-iteration-count: infinite;
  animation-direction: alternate; }

@keyframes changescale {
  from {
    transform: scale(1); }
  to {
    transform: scale(1.3); } }

/*
 slide style
 */
.slide {
  display: block;
  width: 900px;
