/*
 hint
 */
.hint {
  opacity: 0;
  animation: fadeOutUp 0.6s ease forwards;
  color: rgba(255, 255, 255, 0.8);
  background-color: #3e4852;
  font-size: 20px;
  text-align: center;
  width: 400px;
  min-height: 100px;
  line-height: 100px;
  position: fixed;
  left: 25px;
  bottom: 14%;
  z-index: 9999;
  border-radius: 3px;
  filter: drop-shadow(0 8px 8px rgba(0, 0, 0, 0.3)); }

.hint b {
  color: #e5b560; }

.mobile-hint {
  opacity: 0;
  animation: fadeOutUp 0.6s ease forwards;
  text-align: center;
  color: #3e4852;
  width: 128px;
  position: fixed;
  left: calc(50% - 64px);
  bottom: 10%; }

.mobile-hint img {
  display: block;
  margin: 0 auto;
  width: 48px; }

.mobile-hint span {
  display: block;
  margin-top: 5px; }

.show .hint, .show .mobile-hint {
  animation: fadeInDown 0.6s ease 1s forwards; }

.impress-enabled .hint, .impress-enabled .mobile-hint {
  display: block; }

/*
 animation
 */
@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate3d(0, -100%, 0); }
  to {
    opacity: 1;
    transform: none; } }

@keyframes fadeOutUp {
  from {
    opacity: 1; }
  to {
    opacity: 0;
    transform: translate3d(0, 100%, 0); } }

