.navbar-nav {
  width: 100% !important;
  display: grid;
  grid-template-columns: repeat(5, minmax(75px, 175px)) minmax(75px, 1fr) minmax(75px, 150px);
  grid-column-gap: 15px;
  padding: 0 !important;
  height: 100% !important; }
  .navbar-nav.small {
    display: grid;
    grid-template-columns: repeat(3, auto); }
    .navbar-nav.small .item:nth-child(1) {
      display: flex;
      justify-content: flex-start; }
      .navbar-nav.small .item:nth-child(1) .shop-icon {
        cursor: pointer; }
    .navbar-nav.small .item:nth-child(2) {
      display: flex;
      justify-content: center; }
    .navbar-nav.small .item:nth-child(3) {
      display: flex;
      justify-content: flex-end !important;
      margin-left: 0 !important; }
  .navbar-nav .navlink {
    display: flex;
    margin: 0 !important;
    padding: 0 !important;
    height: 100% !important;
    justify-items: center;
    text-align: center; }

.nav-bar {
  background-color: transparent !important;
  position: fixed !important;
  width: 100% !important; }
